body {
    margin: 0;
}



#bar {
    width: 100vw;
    height: 50px;
    position: fixed;
    top: 0;
    z-index: 1;

    display: flex;
    justify-content: space-around;
    align-items: center;

    background-color: rgb(0, 0, 0, 0.1);
    -webkit-backdrop-filter: saturate(180%) blur(20px);
            backdrop-filter: saturate(180%) blur(20px);

}


/* 每个导航栏按钮 */
.barLink {
    font-size: 22px;
    font-weight: 700;
    color: white;

    text-decoration: none;
}
.footer {
    width: 100vw;
    height: 70px;

    background-color: rgb(288, 4, 81);

    display: flex;
    justify-content: center;
    align-items: center;
}
#basic {
    width: 100vw;
    height: 100vh;

    background-image: url(../../static/media/abstract06.38aed89a.jpg);
    background-position: center;

    display: flex;
    justify-content: center;
    align-items: center;
}

/* 基本介绍，在中间部分的 */
#basic-ticket {
    display: flex;
    flex-direction: row;

    color: white;
}
#head-portrait {
    height: 250px;
    width: 250px;
    background-image: url(../../static/media/Eric.379b3677.jpg);
    background-position: center;
    background-size: 375px;
    border-radius: 50%;
    background-repeat: no-repeat;
}
#information {
    width: 415px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-items: center; */

    margin: 0 50px;
}

#border {
    width: 300px;

    border: 1px solid white;
}
h1 {
    font-weight: 800;
}

.contact-mode {
    margin: 10px;
}

.contact-mode > a{
    text-decoration: none;
    color: #ffffff;
    font-size: 16px;
    font-weight: 500;
    padding: 12px 22px;
    border: 2px solid #fff;
    margin-right: 11px;
    margin-left: 11px;
}

.contact-mode > a:hover {
    letter-spacing: 1.5px;
    background-color: #8B2252;
}
.skills {
    width: 100vw;

    margin: 20px 0px 50px;
}


.skill-tickets {
    width: 100%;

    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-row-gap: 20px;
    grid-column-gap: 20px;
    justify-items: center;

}

.skills-title {
    margin: 16px 23px;
}
.skill {
    border: 1px solid #dee2e6!important;
    font-family: "Rubik", sans-serif;
    width: 270px;
    border-radius: 5px;
    padding: 2px 25px 10px;
}
.skill-title {
    display: flex;
    justify-content: space-between;
}

.progress-bar {
    width: 100%;

    display: flex;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    color: #fff;
    text-align: center;
    white-space: nowrap;
    background-color: #007bff;
    transition: width .6s ease;

    background-color: #20c997;

}

.progress {
    display: flex;
    height: 1rem;
    overflow: hidden;
    line-height: 0;
    font-size: .75rem;
    background-color: #e9ecef;
    border-radius: .25rem
}

@media (prefers-reduced-motion:reduce) {
    .progress-bar {
        transition: none
    }
}

.progress-sm {
	height: 0.5rem !important;
}
.displayWall {
    width: 100vw;
    height: 100vh;

    display: flex;
    justify-content: center;
    align-items: center;
}
#porfolioPage {
    min-height: 100vh;
}
